@import url('https://fonts.googleapis.com/css2?family=Saira:wght@100;300;600;800&display=swap');

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  font-size: 100%;
}

html {
  height: 100%;
  overflow: auto;
}
body {
  height: 100%;
}

/* ROOT FONT STYLES */

* {
  font-family: 'Saira', sans-serif;
  color: #333447;
  line-height: 1.5;
}

#dev-container {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: row;
  width: 100%;
  height: 60px;
}

#dev-anim {
  box-sizing: border-box;
  padding: 0 16px;
  position: relative;
  overflow: hidden;
  max-width: 400px;
  box-shadow: 0 8px 12px 12px rgba(0, 0, 0, 0.035);
}

#we-develop div {
  margin-left: 4px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 1.2rem;
  height: 60px;
}

#we-develop strong {
  font-weight: 600;
}

#career-cards {
  max-width: 750px;
  width: 100%;
  margin-bottom: 16px;
}

.job {
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: flex-end;
  padding: 0;
  height: 250px;
  border: 1px solid slateblue;
}

.job .content {
  padding: 16px;
  height: 80px;
  color: #fff;
}

.job p {
  padding: 16px;
}

.job a {
  position: absolute;
  right: 12px;
  top: 8px;
  font-size: 14px;
  font-weight: 600;
  color: #9a56b6;
}

#opn {
  transition: scale 2s ease;
  animation-name: him;
  animation-duration: 2s;
  animation-iteration-count: infinite;
}

@keyframes him {
  from {
    scale: 1;
  }
  to {
    scale: 1.1;
  }
}
